/* --- Buttons --- */

.AllButton {
  background-color: #e5e5e5;
  color: white;
  border-width: 0;
  border-radius: 10px;
  width: 100%;
  margin: 5px 0px;
  padding: 12px 14px;
  text-align: left;
  height: 50px;
}

.Choice {
  background-color: #966c51;
  color: white;
  transition-duration: 0.4s;
}

.Choice:hover {
  background-color: #4e3629; /* same as website */
  cursor: pointer;
}

.ButtonGroup {
  position: relative;
}

.ChoiceBear {
  width: 35px !important;
  height: 35px !important;
  /* margin: 0 !important; */
  /* padding-right: 5px; */
  margin: 0 !important;
  position: absolute;
  /* top: 80px; */
  right: 15px;
  bottom: 12px;
  z-index: 999;
  border-radius: 0 !important;
}

.GreatJobBear {
  width: 35px !important;
  height: 35px !important;
  margin: 0 !important;
  border-radius: 0 !important;
}

.Correct {
  background-color: #8ac277 !important;
}

.IncorrectCorrect {
  border: solid #8ac277;
  border-width: 2px;
  color: rgb(88, 88, 88);
}

.Incorrect {
  background-color: #e55550 !important;
}

.Other {
  color: rgb(88, 88, 88);
}

/* --- Question Group CSS --- */

.Question {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  margin: 0 auto;
  width: 75%;
  max-width: 600px;
  position: relative;
}

.Question img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  margin-bottom: 20px;
}

.num {
  position: absolute;
  top: 1px;
  left: calc(-40px - 0.1vw);
}

.Options {
  margin-top: 15px;
  width: 100%;
}

/* --- More info CSS --- */

.MoreInfo {
  text-align: right;
  width: 100%;
  /* background-color: blue; */
}

.MoreInfo p {
  text-align: left;
  padding: 0 0 10px 15px;
  margin: 16px 0 8px 0;
  width: 60%;
  font-size: calc(16px + 0.3vw);
}

.MoreInfo a {
  text-decoration: none;
  color: inherit;
  transition-duration: 0.4s;
  line-height: calc(18.5px + 0.5vw);
  font-weight: 600;
  font-family: "freight-text-pro", "Times", serif;
  font-size: calc(18px + 0.3vw);
}

.MoreInfo a:hover {
  font-size: calc(20px + 0.3vw);
  font-weight: 700;
}

/* fonts */

/* questions */
h4 {
  font-family: "freight-text-pro", "Times", serif;
  margin: 0;
  padding: 0 0 0 5px;
  font-size: calc(20px + 0.5vw);
}

/* other */
button,
p {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  text-align: left;
  font-size: calc(14px + 0.3vw);
  transition-duration: 0;
}

hr {
  margin: min(10%, 75px) auto;
  width: 75%;
  max-width: 600px;
}

#logo {
  width: calc(300px + 35%);
  margin: 0 auto;
  display: block;
  padding-top: 30px;
}

.score {
  text-align: center;
}

.Main {
  padding-bottom: 50px;
}

#date {
  text-align: center;
  padding-top: 0;
  margin-top: 0;
  margin-bottom: 30px;
  font-family: "freight-text-pro", "Times", serif;
}

#intro {
  text-align: center;
  margin-bottom: 7vh;
}

#top {
  margin-top: 35px;
  padding: 0;
}

.SliderBlock {
  display: flex;
  width: 100%;
  padding: 15px 0px;
}

.Slider {
  margin-right: 20px;
}

.SliderQ .AllButton {
  width: 100px;
  text-align: center;
}
